/* Hacky way to fix black calendar icon */
::-webkit-calendar-picker-indicator {
    filter: invert(1);
}

.sv_q_checkbox {
    text-align: left !important;
}

.App-content-3 {
    margin: 0 !important;
    max-width: calc(100%) !important;
}

.sv_q_radiogroup_inline {
    display: block !important;
}

.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #000;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.sv_page_title {
    color: #cfb991;
    text-align: left;
}

.sv_q_required_text {
    color: red;
    font-weight: 800;
    font-size: 2em!important;
}

.sv_q_radiogroup {
    text-align: left;
}

.sv_main .sv_container .sv_body .sv_page_title span {
    font-size: 1.5em;
}

.sv_main .sv_container .sv_body span:not([class*='select2-selection__choice__remove']):not([class*='select2-results__option']) {
    font-size: 1.2em;
}

.sv_main .sv_container input:placeholder-shown {
    color: #9d9795!important;
    -webkit-text-fill-color: #9d9795!important;
}

.sv_main .sv_container input, textarea {
    color: #fff!important;
    -webkit-text-fill-color: #fff!important;
}

.select2-results__option {
    font-size: 1rem!important;
}
